/* meow-script-regular - latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: "Meow Script";
    font-style: normal;
    font-weight: 400;
    src: url("./../../public/fonts/meow-script-v5-latin-regular.woff2")
            format("woff2"),
        /* Chrome 36+, Opera 23+, Firefox 39+ */
            url("./../../public/fonts/meow-script-v5-latin-regular.woff")
            format("woff"); /* Chrome 5+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* ledger-regular - cyrillic */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: "Ledger";
    font-style: normal;
    font-weight: 400;
    src: url("./../../public/fonts/ledger-v16-cyrillic-regular.woff2")
            format("woff2"),
        /* Chrome 36+, Opera 23+, Firefox 39+ */
            url("./../../public/fonts/ledger-v16-cyrillic-regular.woff")
            format("woff"); /* Chrome 5+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

body {
    font-family: "Ledger";
    font-style: normal;
    font-weight: 400;
}

.main-bg {
    background-color: #cccccc;
}

.auth-bg {
    background: url(./../../public/images/rizky-subagja-1k7TnX5GAww-unsplash.jpg) no-repeat;
}

/* Header styles start */
.header {
    background: url(./../../public/images/rizky-subagja-1k7TnX5GAww-unsplash.jpg)
        no-repeat center;
    background-size: cover;
    box-shadow: 0px 2px 20px rgba(21, 24, 36, 0.15993);
    margin: 0 auto;
    max-width: 1320px;
}

.header__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 390px;
    margin: auto;
    padding-bottom: 10px;
    width: 96%;
    max-width: 1265px;
}

.header__logo-link {
    text-decoration: none;
    color: #ffffff;
}

.header__logo-text {
    font-family: "Meow Script";
    font-size: 62px;
}

.header__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: right;
}

.header__item {
    margin-left: 24px;
}

.header__link {
    font-size: 28px;
    line-height: 53px;
    text-decoration: none;
    color: #ffffff;
}

.header__link:hover,
.header__link:focus,
.header__link:active,
.header__logo-link:hover,
.header__logo-link:focus,
.header__logo-link:active {
    opacity: 0.75;
    color: #fff;
}
/* Header styles end */

/* main section styles start */
.top {
    background: white;
    box-shadow: 0px 2px 20px rgba(21, 24, 36, 0.15993);
    margin: 0 auto;
    padding-bottom: 20px;
    width: 100%;
    max-width: 1320px;
}

.topLoggedOut {
    height: 44.5vh;
}

.top__title {
    max-width: 443px;
    font-size: 36px;
    text-align: center;
    padding-top: 50px;
    margin: auto;
    margin-bottom: 50px;
}

.top__titleLoggedOut{
    display: flex;
    align-items: center;
    height: 100%;
}

.no__favs{
    max-width: 900px;
    font-size: 32px;
    text-align: center;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50%;
}

.no__favsCategories{
    margin-top: 76.5px;
    margin-bottom: 76.5px;
}

.wrapper-main {
    margin: auto;
    max-width: 1290px;
}

.filter__buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.filter__btn {
    font-size: 32px;
    font-family: Ledger;
    border: solid 1px #333333;
    background: white;
    color: #333333;
    padding: 8px 16px;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 10px;
}

.filter__btn--active {
    background: #333333;
    color: white;
}

.filter__btn:hover,
.filter__btn:active {
    background: #333333;
    color: white;
}

.top__cards {
    display: flex;
    flex-wrap: wrap;
    margin-top: 34px;
}

.top__card {
    margin: 10px;
    position: relative;
}

.top__card-thumb {
    box-shadow: 0px 2px 20px rgba(56, 95, 59, 0.15993);
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.top__card-stats {
    position: absolute;
    text-align: center;
    background: rgba(0, 0, 0, 0.3);
    bottom: 0;
    width: 100%;
    padding-bottom: 5px;
}

.top__card-fulltitle {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 10px;
}

.top__card-title {
    color: #fff;
    max-width: 55%;
    word-wrap: break-word;
}

.like-button {
    position: absolute;
    right: 30px;
    font-size: 32px;
    color: white;
    cursor: pointer;
}

.like-button--active {
    color: #333333;
}

.like-button:hover,
.like-button:active {
    color: #333333;
}

.top__card-ingr {
    color: #fff;
    display: block;
    overflow: hidden;
    max-height: 0;
    transition: max-height 1000ms;
}

.top__card:hover .top__card-ingr {
    max-height: 7em;
    transition: max-height 0;
}
/* main section styles end */

/* footer styles start */
footer {
    background-color: #333333;
    box-shadow: 0px 2px 20px rgba(21, 24, 36, 0.15993);
    margin: 0 auto;
    max-width: 1320px;
}

.footer__text {
    text-align: center;
    color: #ffffff;
    font-size: 24px;
    line-height: 55px;
}
/* footer styles end */


/*page 404 start*/
.content {
    margin: 15px auto;
    background: url("./../images/White-Dinner-Plate-PNG-Picture.png") no-repeat;
    background-size:cover;
    text-align: center;
    display: table;
    color: black;
}

.text{
    vertical-align: middle;
    display: table-cell;
}

.error {
    font-weight: 700;
    opacity: 0.7;
    margin-bottom: -10px;
}

.not-found {
    opacity: 0.7;
    padding-bottom: 15px;
}

.back__link404 {
    color: black;
    opacity: 0.7;
    font-size: 20px;
    font-style: italic;
    text-decoration: none;
}

.back__link404:hover, .back__link404:active, .back__link404:focus{
    text-decoration: underline;
    color: black;
    opacity: 0.5;
}
/*page 404 end*/


/*медиа-запросы start*/
@media screen and (max-width: 767px) {
    .header__list {
        flex-direction: column;
        align-items: flex-end;
    }
    .top__cards {
        justify-content: center;
    }
    .top__card {
        width: 410px;
        height: 500px;
    }
    .top__card-title {
        font-size: 32px;
    }
    .top__card-fulltitle {
        min-height: 89px;
    }
    .top__card-ingr {
        font-size: 24px;
        line-height: 29px;
    }
}

@media screen and (max-width: 776px) {
    .header__list {
        flex-direction: column;
        align-items: flex-end;
    }
    .content {
        width: 500px;
        height: 500px;
    }
    .error {
        font-size: 150px;
    }
    .not-found {
        font-size: 26px;
    }
}

@media screen and (min-width: 768px) {
    .top__card {
        width: 328px;
        height: 400px;
    }
    .top__card-title {
        font-size: 28px;
    }
    .top__card-fulltitle {
        min-height: 81px;
    }
    .top__card-ingr {
        font-size: 20px;
        line-height: 22px;
    }
    .content {
        width: 600px;
        height: 600px;
    }
    .error {
        font-size: 170px;
        margin-bottom: -10px;
    }
    .not-found {
        font-size: 28px;
        padding-bottom: 15px;
    }
}

@media screen and (min-width: 992px) {
    .top__card {
        width: 292px;
        height: 356px;
    }
    .top__card-title {
        font-size: 23px;
    }
    .top__card-fulltitle {
        min-height: 71px;
    }
    .top__card-ingr {
        font-size: 18px;
        line-height: 20px;
    }
    .content {
        width: 700px;
        height: 700px;
    }
    .error {
        font-size: 190px;
    }
    .not-found {
        font-size: 32px;
    }
}

@media screen and (min-width: 1200px) {
    .top__card {
        width: 352px;
        height: 429px;
    }
    .top__card-title {
        font-size: 28px;
    }
    .top__card-fulltitle {
        min-height: 81px;
    }
    .top__card-ingr {
        font-size: 20px;
        line-height: 25px;
    }
    .content {
        width: 800px;
        height: 800px;
    }
    .error {
        font-size: 210px;
    }
    .not-found {
        font-size: 36px;
    }
}

@media screen and (min-width: 1400px) {
    .top__card {
        min-width: 402px;
        min-height: 490px;
    }
    .top__card-title {
        font-size: 32px;
    }
    .top__card-fulltitle {
        min-height: 89px;
    }
    .top__card-ingr {
        font-size: 24px;
        line-height: 29px;
    }
    .content {
        width: 900px;
        height: 900px;
    }
    .error {
        font-size: 230px;
    }
    .not-found {
        font-size: 40px;
    }
}
/*медиа-запросы end*/